
<template>  
    <h2>Создать пост</h2>
    <form action="" @submit.prevent>

        <my-input v-model:value="post.title" type="text" placeholder="Название поста"/>

        <my-input v-model:value="post.body"  type="text" placeholder="Описание поста"/>

        <my-button @click="createPost">Создать</my-button>

    </form>
</template>

<script>
export default { 
    data() {
        return {
            post: {
                title: '',
                body: '',
            }
        }
    },
    methods: {
        createPost() {
            this.post.id = Date.now()
            this.$emit('create', this.post)
            this.post = {
                title: '',
                body: '',
            }
        },
    }
}
</script>

<style scoped>
*{
    text-align: center;
}
form {
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

</style>