<template>
    <input :value="value" @input="UpdateInput" type="text" class="input">
</template>
<script>
export default {
    name:'MyInput',
    props:{
        value:[String,Number]
    },
    methods: {
        UpdateInput(event){
            this.$emit('update:value',event.target.value)
        }
    },
}
</script>
<style >
.input {
    width: 100%;
    outline: none;
    padding: 10px;
    border-radius: 5px;
    border: 2px solid rgb(1, 214, 168);
    ;
    background-color: transparent;
    display: inline-block;
}

</style>