<template lang="">
    <div class='dialog' v-if='show' @click='hideDialog'>
        <div class="dialog__content" @click.stop>
            <slot>

            </slot>
        </div>
    </div>
</template>
<script>
export default {
    name:'my-dialog',

    props:{
        show:{
            type:Boolean,
            default:false,
        }
    },
    methods: {
        hideDialog(){
            this.$emit('update:show',false)
        }
    },
}
</script>
<style scoped>
    .dialog{
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        position: fixed;
        background-color: rgba(0,0,0,0.3);
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .dialog__content{
        margin: 0;
        background-color: white;
        border-radius: 12px;
        min-height: 50px;
        height: fit-content;
        padding: 30px;
        min-width: 50px;
    }
</style>