<template>
    <div>
        <h1>Страница с постами</h1>
        <my-input v-model:value='searchQuery' type="text" placeholder="Поиск по названию" style="margin-top: 20px;" />
        <div class="app__btns">
            <my-button @click="ShowDialog" style="margin: 20px 0;">Создать пост</my-button>
            <my-select v-model="selectedSort" :options="sortOptions" />
        </div>

        <my-dialog v-model:show="dialogvisible">
            <post-form @create="createPost" />
        </my-dialog>

        <post-list v-if="!isLoadingPost" :posts="sortedAndSearchedPost" @DeleteThis="DeleteThis" />
        <div v-else>Идет загрузка</div>
        <div ref="observer" class="observer"></div>
        <!-- <div class="page__wrapper">
            <div v-for="page in totalPage" class="page" @click="changePage(page)" :key="page" :class="{
                'current': this.page == page
                
            }">{{page}}</div>
        </div> -->
    </div>
</template>

<style>
.page__wrapper {
    display: flex;
    margin-top: 10px;
}

/* .page{
    padding: 10px;
    background-color:rgb(1, 214, 168) ;
    cursor: pointer;
    text-align: center;
    width: 100%;
    color: white;
    border: 1px solid rgb(1, 214, 168);
    transition:.3s;
}
.page:hover{
    background-color: white;
    color: rgb(1, 214, 168);
}
.page.current{
    background-color: white;
    color: rgb(1, 214, 168);
} */

.observer {
    height: 30px;
    background-color: aquamarine;
}
</style>


<script>
import PostForm from "@/components/PostForm"
import PostList from "@/components/PostList"
import axios from 'axios'

export default {
    components: {
        PostList,
        PostForm
    },
    data() {
        return {
            posts: [
            ],
            body: '',
            title: '',
            dialogvisible: false,
            selectedSort: "",
            searchQuery: '',
            page: 1,
            limit: 10,
            totalPage: 0,
            sortOptions: [
                { value: 'title', name: 'По названию' },
                { value: 'body', name: 'По  описанию' },
                { value: 'id', name: 'По  номеру' }
            ],
            isLoadingPost: false,
        }
    },
    methods: {
        createPost(post) {
            this.posts.push(post)
            this.dialogvisible = false;
        },
        DeleteThis(post) {
            this.posts = this.posts.filter(p => p.id !== post.id)
        },
        ShowDialog() {
            this.dialogvisible = true;
        },
        // changePage(page){
        //     this.page = page;
        // },
        async fetchPosts() {
            try {
                this.isLoadingPost = true;
                const response = await axios.get('https://jsonplaceholder.typicode.com/posts', {
                    params: {
                        _page: this.page,
                        _limit: this.limit,
                    }
                })
                this.totalPage = Math.ceil(response.headers['x-total-count'] / this.limit)
                this.posts = response.data
            } catch (error) {
                alert('Ошибка');
            } finally {
                this.isLoadingPost = false;
            }
        },
        async LoadPosts() {
            try {
                this.page+=1;
                const response = await axios.get('https://jsonplaceholder.typicode.com/posts', {
                    params: {
                        _page: this.page,
                        _limit: this.limit,
                    }
                })
                this.totalPage = Math.ceil(response.headers['x-total-count'] / this.limit)
                this.posts = [...this.posts, ...response.data]
            } catch (error) {
                alert('Ошибка');
            } finally {
            }
        },


    },
    mounted() {
        this.fetchPosts();
        let options = {
            rootMargin: "0px",
            threshold: 1.0,
        };
        const callback = (entries,observer)=>{
            if(entries[0].isIntersecting && this.page <this.totalPage){
                console.log('пересечен')
                this.LoadPosts();
            }
        }
        let observer = new IntersectionObserver(callback, options);
        observer.observe(this.$refs.observer)
    },
    computed: {
        sortedPost() {
            return [...this.posts].sort((a, b) => {
                if (typeof a[this.selectedSort] == 'string')
                    return a[this.selectedSort]?.localeCompare(b[this.selectedSort])
                else {
                    if (a[this.selectedSort] > b[this.selectedSort]) return 1;
                    if (a[this.selectedSort] == b[this.selectedSort]) return 0;
                    if (a[this.selectedSort] < b[this.selectedSort]) return -1;
                }
            })
        },
        sortedAndSearchedPost() {
            return this.sortedPost.filter(post => post.title.toLowerCase().includes(this.searchQuery.toLowerCase()))
        }
    },
    watch: {
        // page(){
        //     this.fetchPosts();
        // }
    }

}
</script>