<template lang="">
    <div>
        <select class='select' :value="modelValue" @change="UpdateModelValue">
            <option disabled value="">Выберите из списка</option>
            <option v-for="option in options" :key="option.value" :value="option.value">{{option.name}}</option>
        </select>
    </div>
</template>
<script>
export default {
    name:'my-select',
    props:{
        modelValue:{
            type:String,
        },
        options:{
            type:Array,
            default:()=>[],
        }
    },
    methods: {
        UpdateModelValue(event){
            this.$emit('update:modelValue',event.target.value)
        }
    },
}
</script>
<style lang="css">
    .select{
        border: 2px solid rgb(1, 214, 168);
        outline: none;
    }
</style>